let initialDemocracyState = {
  scroll:0,
  percent:0,
  direction:null,
  currentStory:0,
  scaleTriangle:false,
  scenes:[
    'Intro',
    'NetworkTitle',
    'NetworkContent',
    'Scanner_one',
    'Connections',
    'Preperations',
    'History',
    'HistoryContent',
    'Politics',
    'Shadow',
    'Persistent',
    'PersistentContent',
    'Ethan_one',
    'Ethan_two',
    'Conclusion',
    'Static'
  ]
}

const democracyReducer = (state, action) => {
  switch (action.type) {

    case 'SET_SCALE_TRIANGLE':
      let SET_SCALE_TRIANGLE = {...state}
      SET_SCALE_TRIANGLE.scaleTriangle = action.payload
    return SET_SCALE_TRIANGLE;

    case 'SET_STORY':
      let SET_STORY = {...state}
      SET_STORY.currentStory = action.payload
    return SET_STORY;

    case 'SET_DIRECTION':
      let SET_DIRECTION = {...state}
      SET_DIRECTION.direction = action.payload
    return SET_DIRECTION;

    case 'SET_SCROLL':
      let SET_SCROLL = {...state}
      if(SET_SCROLL.scroll !== action.payload)SET_SCROLL.scroll = action.payload
    return SET_SCROLL;

    case 'SET_PERCENT':
      let SET_PERCENT = {...state}
      if(SET_PERCENT.percent !== action.payload)SET_PERCENT.percent = action.payload
    return SET_PERCENT;


    default: throw new Error('Unexpected action');
  }
}

export default democracyReducer
export { initialDemocracyState }
